import {
  FunctionComponent,
  Ref,
  forwardRef,
  ChangeEvent,
  useCallback,
  PropsWithoutRef,
  DragEvent,
  useState
} from 'react';
import styled, { css } from 'styled-components';
import { hideVisually } from 'polished';

import { BaseProps, ForwardProps, NoChildrenProp, PropsWithDefaults } from '../../types';
import { useOverride, useConsolidatedRef, useI18n, useUID, useDirection } from '../../hooks';
import FormControl, { FormControlProps } from '../FormControl';
import { defaultThemeProp } from '../../theme';
import Flex from '../Flex';
import Text from '../Text';
import Icon, { registerIcon } from '../Icon';
import * as paperClipIcon from '../Icon/icons/paper-clip.icon';
import FormField from '../FormField';
import Grid from '../Grid';

import FileUploadItem, { FileUploadItemProps } from './FileUploadItem';

registerIcon(paperClipIcon);

type FileInputPropsWithDefaults = PropsWithDefaults<FileInputProps>;

export interface FileInputProps extends BaseProps, NoChildrenProp {
  /**
   * Called when files are added either via the input or drop zone.
   */
  onFilesAdded?: (files: File[]) => void;
  /**
   * Allow multiple files to be selected from the OS specific file browser.
   * NOTE: This does not restrict multiple files from being added via drag and drop.
   * Restrict multi file drag and drop via onFilesAdded and custom info message.
   * @default false
   */
  multiple?: boolean;
  /** An array of files that have been uploaded. */
  files?: FileUploadItemProps[];
  /**
   * Sets DOM id for the control and associates label element via 'for' attribute.
   * If an id is not pass, a random id will be generated for any render.
   */
  id?: FormControlProps['id'];
  /** Set visual state based on a validation state. */
  status?: FormControlProps['status'];
  /** Pass a string or a fragment with an Icon and string. */
  label?: FormControlProps['label'];
  /** Visually hides the label region. */
  labelHidden?: FormControlProps['labelHidden'];
  /** It is recommended to pass a simple string to offer guidance. Text will be styled based on status prop. */
  info?: FormControlProps['info'];
  /** Indicate if the field is required. The browser defaults to false. */
  required?: FormControlProps['required'];
  /** Disable the control. The browser defaults to false. */
  disabled?: FormControlProps['disabled'];
  /** Sets html name attribute for the underlying control. Useful for mapping to a data field. */
  name?: FormControlProps['name'];
  /** Ref for the input element within the component's dom structure. */
  ref?: Ref<HTMLInputElement>;
}

export const StyledDropZone = styled(FormControl)<{ dragOver: boolean }>(({ dragOver, theme }) => {
  return css`
    padding: calc(2 * ${theme.base.spacing});
    min-height: 4rem;
    border-style: dashed;
    cursor: pointer;

    ${dragOver &&
    css`
      border-style: solid;
    `}
  `;
});

export const StyledFileInput = styled.div<FileInputPropsWithDefaults & ForwardProps>(
  ({ theme }) => {
    return css`
      position: relative;

      input {
        ${hideVisually}
      }

      input:enabled:focus + ${StyledDropZone} {
        box-shadow: ${theme.components['form-control'][':focus']['box-shadow']};
      }

      input[hidden] + ${StyledDropZone} {
        display: none;
      }
    `;
  }
);

StyledFileInput.defaultProps = defaultThemeProp;

const StyledInteractiveText = styled.span(({ theme }) => {
  return css`
    color: ${theme.base.palette.interactive};
  `;
});

StyledInteractiveText.defaultProps = defaultThemeProp;

const BaseFileInput: FunctionComponent<FileInputProps & ForwardProps> = forwardRef(
  (props: PropsWithoutRef<FileInputProps>, ref: FileInputProps['ref']) => {
    const uid = useUID();
    const {
      id = uid,
      label,
      labelHidden,
      info,
      required = false,
      disabled = false,
      files,
      onFilesAdded,
      multiple = false,
      status,
      ...restProps
    } = props;

    const icon = <Icon name='paper-clip' />;
    const inputRef = useConsolidatedRef(ref);

    const onChange = useCallback(
      (e: ChangeEvent<HTMLInputElement>) => {
        if (e.target.files) {
          onFilesAdded?.(Array.from(e.target.files));
        }
        /**
         * WHY are we doing this?
         * We are not working with the "value" prop of the input but rather the File instances that are created.
         * The consumer of this component should manage an array or File(s) and allow for a user to remove a selected file.
         * Since we can not modify the input value of files for browser security reasons,
         * and since setting the files property is less than ideal, we reset/toggle the input to a fresh state after a render.
         * This enables onChange to fire even if the user had just previously selected a file, removed it, and selected it again.
         */
        if (inputRef.current) inputRef.current.value = '';
      },
      [onFilesAdded]
    );

    const t = useI18n();
    const [dragOver, setDragOver] = useState(false);
    const { ltr, rtl } = useDirection();

    const fileInput = (
      <StyledFileInput>
        <input
          {...restProps}
          ref={inputRef}
          type='file'
          id={id}
          disabled={disabled}
          onChange={onChange}
          multiple={multiple}
        />
        <Flex
          container={{ alignItems: 'center', justify: 'center' }}
          item={{ grow: 1 }}
          as={StyledDropZone}
          dragOver={dragOver}
          onClick={() => {
            inputRef.current?.focus();
            inputRef.current?.click();
          }}
          onDragOver={(e: DragEvent<HTMLDivElement>) => {
            e.preventDefault();
          }}
          onDragEnter={() => setDragOver(true)}
          onDragLeave={() => setDragOver(false)}
          onDrop={(e: DragEvent<HTMLDivElement>) => {
            e.preventDefault();
            setDragOver(false);
            onFilesAdded?.(Array.from(e.dataTransfer.files));
          }}
        >
          {ltr && icon}
          <Text variant='primary'>
            &nbsp;
            {t('file_upload_text_main', [
              <StyledInteractiveText>
                {t(multiple ? 'file_upload_text_multiple' : 'file_upload_text_one')}
              </StyledInteractiveText>
            ])}
            &nbsp;
          </Text>
          {rtl && icon}
        </Flex>
      </StyledFileInput>
    );

    return (
      <Grid container={{ rowGap: 1 }}>
        {label ? (
          <FormField {...{ label, labelHidden, id, info, required, disabled, status }}>
            {fileInput}
          </FormField>
        ) : (
          fileInput
        )}
        {!!files?.length && (
          <Grid
            container={{
              gap: 1,
              cols: 'repeat(2, minmax(0, 1fr))'
            }}
            as='ul'
          >
            {files.map(({ name, ...file }, index) => {
              // eslint-disable-next-line react/no-array-index-key
              return <FileUploadItem key={index} name={name} {...file} />;
            })}
          </Grid>
        )}
      </Grid>
    );
  }
);

const FileInput: typeof BaseFileInput = forwardRef((props, ref) => {
  const Override = useOverride('FileInput');
  if (Override) return <Override {...props} ref={ref} />;

  return <BaseFileInput {...props} ref={ref} />;
});

export default FileInput;
